@font-face {
  font-family: 'iconeTypo';
  src: url('./fonts/Barber\ icone.ttf');
}
@font-face {
  font-family: 'allstars';
  src: url('./fonts/All\ Star\ Resort.ttf');
}
@font-face {
  font-family: 'Gruppo';
  src: url('./fonts/Gruppo-Regular.ttf');
}
