.ligneTop {
  background-color: black;
  height: 20px;
}
.ligneBack {
  background-color: black;
  height: 2px;
  opacity: 0.5;
  margin-top: 5px;
}
.logoDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}
.instagramHeader,
.facebookHeader {
  display: flex;
  align-items: center;
}
.textFcb {
  margin-left: 18px;
}
.fa-black {
  color: black;
}
.socialMediaHeader {
  list-style: none;
  font-family: Gruppo;
  font-weight: 600;
  font-size: 15px;
}
.logo {
  width: 200px;
  padding-left: 150px;
}
.headerNav {
  list-style: none;
  color: black;
  display: flex;
  justify-content: space-evenly;
  font-size: 40px;
  padding: 0;
  margin: 0;
  font-family: allstars;
}
.iconeHeader {
  font-family: iconeTypo;
  margin-right: 10px;
}
.accueil:hover,
.realisation:hover,
.prestations:hover,
.equipe:hover {
  text-shadow: 2px 2px 0 #bcbcbc, 4px 4px 0 #9c9c9c,
    2px -6px 3px rgba(0, 0, 0, 0);
}
a {
  text-decoration: none;
  color: black;
}
a:visited {
  color: black;
}

/* Animation apparition */
.vignets1 {
  animation: fadein 1s;
}
.vignets2 {
  animation: fadein 2s;
}
.vignets3 {
  animation: fadein 3s;
}
.vignets4 {
  animation: fadein 4s;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive Tablette */
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .headerNav {
    font-size: 28px;
  }
}

/* Responsive Téléphone */

@media screen and (max-width: 767px) {
  .logo {
    padding-left: 0px;
  }
  .logoDiv {
    display: flex;
    flex-direction: column;
  }
  .headerNav {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 30px;
  }
  .socialMediaHeader {
    display: flex;
    justify-content: center;
    padding-left: 0;
    text-align: center;
  }
  .instagramHeader,
  .facebookHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
